@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@100;300;400;900&display=swap);
body {
  margin: 0;
  font-family: 'Londrina Solid', cursive;
  letter-spacing: 0.2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  max-height: 100vh;
  text-align: center;
  background-color: #a0d1f9;
}

.season {
  font-size: 1.5rem;
}

.header {
  height: 23h;
}
h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}

a, button {
    font-family: inherit;
    font-size: inherit;
    text-decoration: none;
}

.myButton {
	box-shadow: 3px 4px 0px 0px #1564ad;
	background: linear-gradient(to bottom, #79bbff 5%, #378de5 100%);
	background-color:#79bbff;
	border-radius:5px;
	border:1px solid #337bc4;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:17px;
	font-weight:bold;
	padding:12px 44px;
	text-decoration:none;
	text-shadow:0px 1px 0px #528ecc;
	margin: 1rem;
}
.myButton:hover {
	background:linear-gradient(to bottom, #378de5 5%, #79bbff 100%);
	background-color:#378de5;
}
.myButton:active {
	position:relative;
	top:1px;
}

.showHide {
    display: none;
}
.raining{
    position: absolute;
    background-color: rgb(70, 70, 136);
    border-radius: 50%;
    border: 3px solid #FFFFFF;
    width: 40px;
    height: 40px;
    top: 10px;
    left: 10px;
    box-shadow: 5px 5px 15px 5px rgba(70, 70, 70, 0.12);
}

.imgIcon{
    width: 30px;
    padding-top: 5px;
}
.progressContainer{
    text-align: center;
    justify-content: center;
    margin-top: 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin: 1rem 0.5rem;
}

.bar{
    display: block;
    width: 95%;
    margin-top: 20px;
}
.startScreen {
    cursor: pointer;
    background-image: radial-gradient(circle at center, lightyellow, goldenrod);
    height: 100vh;
    display: grid;
    grid-template-rows: 1fr 1fr;
    place-items: center;
}

.startScreenText{
    padding-bottom: 300px;
    color: white;
    font-size: 3em;
    -webkit-text-stroke: 2px black;
}

.titleScreenText{
    padding-bottom: 300px;
    color: rgb(255, 249, 231);
    font-size: 7em;
    -webkit-text-stroke: 4px rgb(34, 34, 34);
    text-shadow: 1px 3px 0 #969696, 1px 13px 5px #aba8a8;
}


.heartbeat {
	-webkit-animation: heartbeat 1.5s ease-in-out infinite both;
	        animation: heartbeat 1.5s ease-in-out infinite both;
